export default {
  name: 'myPagination',
  props: {
    pageSizes: {
      default: function () {
        return [10, 20, 30, 50, 100];
      },
      type: Array
    },
    pageSize: {
      default: 20,
      type: Number
    },
    total: {
      default: 0,
      type: Number
    },
    currentPage: {
      default: 1,
      type: Number
    },
    background: {
      default: true,
      type: Boolean
    },
    small: {
      default: false,
      type: Boolean
    },
    showJump: {
      default: true,
      type: Boolean
    },
    layout: {
      default: 'total, sizes, prev, pager, next, jumper, slot',
      type: String
    }
  },
  data() {
    return {

    }
  },
  methods: {
    handleSizeChange(data) {
      this.$emit('sizeChange', data);
    },
    handleCurrentChange(data) {
      this.$emit('currentPage', data);
    },
    handleJumpSizeChange(data){
      this.$emit('jumpChange', this.currentPage);
    }
  }
}
